






































import {
	mapMutations
} from 'vuex'
export default {
	data() {
		return {
			// 表单数据
			info: {
				mobile: "", // 手机号码
				password: "" // 密码
			},
			isOpen: false // 密码眼睛是不是开着
		};
	},
	methods: {
		...mapMutations(['login']),
		// 检验输入的数据
		chechInfo() {
			if (!this.$config.reg.mobileReg.test(this.info.mobile)) {
				uni.showToast({
					icon: "none",
					title: '请输入正确的手机号码',
					duration: 2000
				})

				return false
			}

			if (!this.$config.reg.passwordReg.test(this.info.password)) {
				uni.showToast({
					icon: "none",
					title: '请输入6-20位密码',
					duration: 2000
				})

				return false
			}
			return true
		},
		// 登录
		login() {
			if (!this.chechInfo()) {
				return false
			}

			this.$public.API_GET({
				url: "loginWithPwd",
				data: {
					mobile: this.info.mobile,
					pwd: this.info.password
				},
				success: (res) => {
					console.log(res)
					if (!res.success) {
						uni.showToast({
							icon: 'none',
							title: res.data.msg
						});
						return false;
					}

					this.login(res.data.data);
				}
			});
		},
	}
}
